<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <dynamic-form lang="en" ref="AddConfig" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            <template v-slot:tgslot-CanViewAllCrmRecord="{ data }">
                <label class="switch">
                    <input class="form-check-input rdoticketfor custom-control-input dynamic" type="checkbox" name="Can_View_CRM_record" ref="theCheckbox">
                    <span class="slider round">
                        <span class="slider-yes">Yes</span>
                        <span class="slider-no">No</span>
                    </span>
                </label>
            </template>
        </dynamic-form>
      
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default{
    created(){
        var vm=this;
        vm.GetUser();
    },
    data(){
        return{
            UserData:[],
            FormSchema: [
                    {
                        layoutType: "double",
                        Data: [
                            {
                                astype: "SelectField",
                                label: this.$t('User'),
                                name: "User",
                                value: "",
                                placeholder: this.$t('User'),
                                config: {
                                    options: [],
                                    onChange: this.onChangeProfile,
                                },
                                validationRules: {
                                    "required": false
                                },
                            },
                            {
                                astype: "SlotField",
                                name: "CanViewAllCrmRecord",
                                value: "Yes",
                                placeholder: "",
                                validationRules: { "required": false},                            
                            },
                        ]
                    }
                ],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-success",
                        text: "<i class='fa fa-save pr-2'></i>Save",
                    }
                ],
        }
    },
    methods:{
        onSubmit:function(){
            var vm=this;
            alert("Hi");
        },
        GetUser:function(){
            var vm=this;
            var param = 'moduleName=CRM_USER&type=USERS';
            DataService.GetDDLDataForAddGroup(param).then(response => {
                debugger
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item,indx)=>{
                    vm.FormSchema[0].Data[0].config.options.push({
                        name:item.name,
                        value:item.value
                    })
                })
                vm.$refs.AddConfig.UpdateKeyValue();
            });
        }
    }
}
</script>
