<template>
    <div class="my-popups modal right fade show" style="display:block" role="dialog">
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog doc-setup-slide">
                <div class="modal-content">
                    <div class="modal-header mheader-user py-0">
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <i aria-hidden="true" class="fa fa-times"></i>
                        </button>
                        <h4 class="modal-title py-3">{{ $t('SetupCRM') }}</h4>
                    </div>
                    <div class="modal-body bg-white">
                        <section class="container-fluid p-0">
                            <div class="left-menu-tab new_tab">
                                <div class="col-md-12 pb-0" v-if="popupCondition=='setup'">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                            <div class="row">
                                                <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile') }}</div>
                                                <div class="col-4 pl-0 text-right"><small>
                                                    <i class="fa fa-check-circle pr-1" v-bind:class="{'text-success':SetupProgresText=='Completed', 'text-muted':SetupProgresText!=='Completed'}"></i>{{SetupProgresText}}</small>
                                                </div>
                                            </div>
                                            <div class="progress smaller my-2">
                                                <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                    id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`"
                                                    :title="SetupProgress">{{ SetupProgress }}%</div>
                                            </div>
                                            <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                                <ul>
                                                    <li>
                                                        <b id="divConsumendStorage"><span>Progress {{CurrentTab}} of {{TotalStep}}</span></b>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="popupCondition=='setup'">
                                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                        <li @click="ShowNextTab(1)" class="nav-item FirstScreen" >
                                            <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}"  style="cursor: pointer;"><i class="fa fa-check-square-o pr-2 text-muted"></i>{{ $t('EnterLead') }}</a>
                                        </li>
                                        <li @click="ShowNextTab(2)" class="nav-item ThirdScreen" >
                                            <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==2}"  style="cursor: pointer;"><i class="fa fa-info-circle font-18 pr-2 text-muted"></i>{{ $t('Configuration') }}</a>
                                        </li>
                                    </ul>
                                </div>
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="popupCondition=='configuration'">
                                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                        <li @click="ShowNextTab(1)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}" style="cursor: pointer;"><i class="fa fa-user pr-2 text-muted"></i>
                                             {{ $t('CRM') }}
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div class="right-content">
                                <div class="col-md-12 p-0" v-if="popupCondition=='setup'">
                                    <AddLeadVue v-if="CurrentTab==1"></AddLeadVue>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition=='setup'">
                                    <ConfigurationTab v-if="CurrentTab==2"></ConfigurationTab>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition=='configuration'">
                                    <Configuration :ModuleId="ModuleId"></Configuration>
                                </div>
                            </div>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import AddLeadVue from './AddLead.vue'
import ConfigurationTab from './ConfigurationTab.vue';
import Configuration from './Configuration.vue';
export default{
   components:{
    AddLeadVue,
    ConfigurationTab,
    Configuration
   },
   props:{
        popupCondition:{
            type:String,
            required:true
        },
   },
   data(){
    return{
        CurrentTab:1,
        ModuleData:[],
        ModuleId:0
    }
   },
   created(){
    var vm=this;
    vm.GetModule();
   },
   methods:{
    ShowNextTab:function(dta){
        var vm=this
        vm.CurrentTab=dta;
    },
    GetModule:function(){
        var vm=this;
        DataService.GetAllModule().then(response => {
            debugger
            vm.ModuleData=response.data;
            vm.ModuleData.forEach((itm,index)=>{
                if(itm.moduleName=='CRM'){
                    vm.ModuleId=itm.moduleId;
                }
            })
        })
    }
   }
}
</script>
